import apiClient from './config';
import { MenuSection, MenuResponse, MenuItem } from '../types/menu';
import { buildQueryString, handleApiError, logApiResponse } from './utils';
import { getCurrentLanguage, SupportedLanguage } from '../config/languageConfig';
import { createCacheKey, getCacheData, setCacheData } from './cacheUtils';

/**
 *    
 * @param restaurantId  ID
 * @param lang   (:   )
 * @returns   
 */
export const getRestaurantMenus = async (
  restaurantId: string | number,
  lang?: string
): Promise<MenuSection[]> => {
  const endpoint = `/api/v1/stores/${restaurantId}/menus`;
  const currentLang = lang || getCurrentLanguage();
  
  try {
    //   
    const params = { lang: currentLang };
    
    //   
    const cacheKey = createCacheKey(endpoint, params);
    
    //   
    const cachedData = getCacheData<MenuSection[]>(cacheKey, currentLang as SupportedLanguage);
    if (cachedData) {
      return cachedData;
    }
    
    //     
    const queryString = buildQueryString(params);
    const response = await apiClient.get(`${endpoint}${queryString}`);
    logApiResponse(endpoint, response.data);
    
    // API     
    const menuData = transformMenuData(response.data);
    
    //   
    setCacheData(cacheKey, menuData, currentLang as SupportedLanguage);
    
    return menuData;
  } catch (error) {
    const apiError = handleApiError(error);
    console.error(`Error fetching restaurant menus: ${apiError.message}`, apiError);
    
    //      
    return [];
  }
};

/**
 *    
 * @param restaurantId  ID
 * @param menuId  ID
 * @param lang   (:   )
 * @returns   
 */
export const getMenuDetail = async (
  restaurantId: string | number,
  menuId: string | number,
  lang?: string
): Promise<MenuItem | null> => {
  const endpoint = `/api/v1/stores/${restaurantId}/menus/${menuId}`;
  const currentLang = lang || getCurrentLanguage();
  
  try {
    //   
    const params = { lang: currentLang };
    
    //   
    const cacheKey = createCacheKey(endpoint, params);
    
    //   
    const cachedData = getCacheData<MenuItem>(cacheKey, currentLang as SupportedLanguage);
    if (cachedData) {
      return cachedData;
    }
    
    //     
    const queryString = buildQueryString(params);
    const response = await apiClient.get(`${endpoint}${queryString}`);
    logApiResponse(endpoint, response.data);
    
    // API     
    const menuItem = transformMenuItemData(response.data);
    
    //   
    setCacheData(cacheKey, menuItem, currentLang as SupportedLanguage);
    
    return menuItem;
  } catch (error) {
    const apiError = handleApiError(error);
    console.error(`Error fetching menu detail: ${apiError.message}`, apiError);
    
    //    null 
    return null;
  }
};

/**
 * API     MenuItem  
 * @param apiMenuItem API    
 * @returns  MenuItem 
 */
function transformMenuItemData(apiMenuItem: any): MenuItem {
  if (!apiMenuItem || typeof apiMenuItem !== 'object') {
    throw new Error('Invalid menu item data');
  }
  
  //  
  const options = Array.isArray(apiMenuItem.options) ? apiMenuItem.options.map((opt: { id: string; name: string; description?: string }) => ({
    id: opt.id,
    name: opt.name,
    slug: opt.id,
    sourceId: 0,
    mandatory: false,
    multiple: false,
    multipleCount: 1,
    isAvailableQuantity: false,
    menuOptionItems: [{
      id: opt.id,
      name: opt.name,
      slug: opt.id,
      sourceId: 0,
      price: 0,
      description: opt.description || '',
      soldout: false,
      isDeposit: false,
      depositPrice: 0,
      depositDescription: ''
    }]
  })) : [];
  
  //   
  return {
    id: apiMenuItem.id,
    name: apiMenuItem.name,
    subtitle: "",
    price: apiMenuItem.price || 0,
    description: apiMenuItem.description || "",
    image: apiMenuItem.image || null,
    originalImage: apiMenuItem.image || null,
    slug: apiMenuItem.id,
    sourceId: 0,
    reviewCount: 0,
    soldout: false,
    oneDish: false,
    menuOptions: options,
    nameEn: apiMenuItem.nameEn || apiMenuItem.name, //       
    descriptionEn: apiMenuItem.descriptionEn || apiMenuItem.description //       
  };
}

/**
 * API     MenuSection  
 * @param apiMenus API   
 * @returns  MenuSection 
 */
function transformMenuData(apiMenus: any[]): MenuSection[] {
  if (!Array.isArray(apiMenus) || apiMenus.length === 0) {
    return [];
  }
  
  //   
  const menusByType: Record<string, MenuItem[]> = {};
  
  // API       
  apiMenus.forEach(item => {
    const menuType = item.type || 'Other';
    
    if (!menusByType[menuType]) {
      menusByType[menuType] = [];
    }
    
    //  
    const options = Array.isArray(item.options) ? item.options.map((opt: { id: string; name: string; description?: string }) => ({
      id: opt.id,
      name: opt.name,
      slug: opt.id,
      sourceId: 0,
      mandatory: false,
      multiple: false,
      multipleCount: 1,
      isAvailableQuantity: false,
      menuOptionItems: [{
        id: opt.id,
        name: opt.name,
        slug: opt.id,
        sourceId: 0,
        price: 0,
        description: opt.description || '',
        soldout: false,
        isDeposit: false,
        depositPrice: 0,
        depositDescription: ''
      }]
    })) : [];
    
    //   
    const menuItem: MenuItem = {
      id: item.id,
      name: item.name,
      subtitle: "",
      price: item.price || 0,
      description: item.description || "",
      image: item.image || null,
      originalImage: item.image || null,
      slug: item.id,
      sourceId: 0,
      reviewCount: 0,
      soldout: false,
      oneDish: false,
      menuOptions: options,
      nameEn: item.nameEn || item.name, //       
      descriptionEn: item.descriptionEn || item.description //       
    };
    
    menusByType[menuType].push(menuItem);
  });
  
  //   MenuSection  
  const menuSections: MenuSection[] = Object.entries(menusByType).map(([type, items], index) => ({
    id: `section-${index}`,
    name: type,
    nameEn: type,
    image: null,
    msType: null,
    slug: type.toLowerCase().replace(/\s+/g, '-'),
    sourceId: index,
    menuItems: items
  }));
  
  return menuSections;
} 